import Vue from 'vue'
import Router from 'vue-router'
import Weather from '@/components/Weather/Weather'
import RandomBox from '@/components/RandomBox/RandomBox'
import Login from '@/components/Login/Login'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Weather',
      component: Weather
    },
    {
      path: '/randomBox',
      name: 'RandomBox',
      component: RandomBox
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
  ]
})



