<template >
    <div id="container" ref="container">
        <input type="text" v-model="id" v-crefs="{ id: 'id' }" placeholder="아이디">
        <input type="text" v-model="pwd" v-crefs="{ id: 'pwd' }" @keyup.13="fnLogin" placeholder="비밀번호">
        <button ref="btnLogin" class="button">로그인</button>
    </div>
</template>
<script>
export default {
    data() {
        return {
            id      : '',
            pwd     : '',
        }
    },
    methods: {
        fnLogin() {
            for (var k in this.$data) {
                if (this.$data[k] === '' || this.$data[k] === undefined) {
                    var refsNick = this.$refs[k].placeholder;
                    this.$refs[k].focus();
                    return alert(refsNick + "를 입력해주세요.");
                }
            }
            // 로그인 로직
            this.fnGetUserInfo();
        },
        fnGetUserInfo() {
            var ansBack = {
                "id"    : '1111',
                "pwd"   : '1111',
            }
            this.fnGetUserInfoAnsBack(ansBack);    
        },
        fnGetUserInfoAnsBack(ansBack) {
            console.info(ansBack)
            var keys = ['id', 'pwd']
            var eCause = ''
            for(var key in keys) {
                // console.info((ansBack[key],  this.$data[key]))
                if(ansBack[key] !== this.$data[key]) {
                    eCause = key
                }
            }
            // console.info(eCause)
            // if(!eCause) {
            //     alert('로그인되었습니다.')
            // } else {
            //     alert('로그인X :' + eCause+ '잘못됨')
            // }
        },

    },
    directives: {
        'crefs': {
            bind(el, binding, vnode) {
                const { value } = binding;
                //이제는 신만이 아는 TODO
                // switch (Object.keys(value).length) {
                //     case 1:  break;
                //     case 2:  break;
                //     case 3:  break;
                //     default: break;
                // }
                const { id } = value;
                if (id) {
                    el.id = id;
                    vnode.context.$refs[id] = el;
                    el.setAttribute('name', id);
                }
            }
        },
        focus: {
            inserted(el) {
                el.focus();
            }
        }
    },
    mounted() {
        var that = this;
        this.$refs.btnLogin.addEventListener('click', function (e) {
            that.fnLogin();
        });
        console.info('ID : 1111', '\nPWD : 1111')
    },
    created() {
        //console.info('cc')
    },
}
</script>
<style>
#container {
    text-align: center;
    color: #fff;
    background: rgb(37, 46, 148);
    background: radial-gradient(circle,
            rgba(37, 46, 148, 1) 0%,
            rgba(0, 0, 0, 1) 100%);
    min-height: 80vh;
    text-align: center;
    padding: 15vh 0 0;
    position: relative;
}

button {
    border: none;
    font-size: 30px;
    padding: 1rem 3rem;
    cursor: pointer;
    text-transform: uppercase;
}

input {
    border-left: none;
    border-right: none;
    border-top: none;
    display: inline-block;
    line-height: 1.6;
    font-size: 20px;
    padding-top: 20px;
    margin: 20px;
    text-align: center;
    outline: none;
}

</style>


