<template>
    <div>
        <random-box v-on:color="colorChange" v-bind:propsdata="mycolor"></random-box>
    </div>
</template>
<script>
import RandomBox from '@/components/RandomBox/RandomBoxDetail'

export default {
    components: {
        RandomBox
    },
    data() {
        return {
            mycolor     : '#' + Math.round(Math.random() * 0xffffff).toString(16),
            play        : ''
        }
    },
    created() {
        this.colorChange();
    },
    methods: {
        colorChange() {
            this.$data.mycolor     = '#' + Math.round(Math.random() * 0xffffff).toString(16);
            console.info('parent_colorChange', this.$data.mycolor);
        },
    }
}
</script>



